<template>
  <div class="side-catalog">
    <div class="head">课程目录概览</div>
    <div class="box">
      <el-collapse accordion class="catalog-collapse">
        <el-collapse-item v-for="(item,index) in list" :key="index">
          <template slot="title">
            <div class="title-item">
              <i class="iconfont icon-shu"></i>
              {{item.title}}
            </div>
          </template>
          <div
            v-for="(sub,j) in item.subList"
            :key="j"
            class="sub-item"
            :class="{'sub-active':sub.active}"
            @click="subClickHandler(sub)"
          >
            <i class="iconfont" :class="{'icon-shuji':sub.active,'icon-shubenhoushu':!sub.active}"></i>
            {{sub.title}}
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>
<script>
export default {
  props: ['list'],
  beforeMount () {
    this.initSub()
  },
  methods: {
    subClickHandler (item) {
      this.initSub()
      this.$set(item, 'active', true)
    },
    initSub () {
      this.list.forEach(element => {
        element.subList.forEach(sub => {
          this.$set(sub, 'active', false)
        })
      })
    }
  }
}
</script>
<style lang="less">
.side-catalog {
  width: 230px;
  height: 727px;
  background: #fff;
  text-align: left;
  user-select: none;
  .head {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #63a9ff;
    color: #fff;
    padding-left: 20px;
  }
  .box {
    width: 100%;
    padding: 28px;
    .catalog-collapse {
      border: none !important;
      .iconfont {
        margin-right: 18px;
      }
      .title-item {
        width: 100%;
        height: 32px;
        line-height: 32px;
        border-radius: 18px;
        background: #f0f3fa;
        padding: 0 20px;
      }
      .sub-item {
        padding-left: 42px;
        &:hover {
          cursor: pointer;
        }
      }
      .el-collapse-item__arrow {
        display: none;
      }
      .el-collapse-item__header,
      .el-collapse-item__wrap {
        border-bottom: none !important;
      }
      .is-active {
        color: #fff;
        .title-item {
          background: #63a9ff;
        }
      }
      .sub-active {
        color: #63a9ff;
      }
    }
  }
}
</style>



