      let params = {
        location: "2",
        key: "",
        sfield: "",
        sdir: "",
        estate: "all",
        pno: 1
      };
      const res = await http.get(api.getSilderPic, params);
      if (res.Result) {
        let downloadURL = await api.GetDownloadFileURL();
        res.Datas.forEach(element => {
          this.bannerImg = `${downloadURL}?filename=${element.SCID}&fileType=7`;
        });
      }
    },
    async fetchCourseBySch() {
      const res = await http.get(api.getCourseBySch, {});
      if (res.Result) {
        if (res.SchoolType === 1) {
          this.$refs.selectBox.gradeordisciplineName = "专业";
        } else {
          this.$refs.selectBox.gradeordisciplineName = "年级";
        }
        this.$refs.selectBox.majorcourseList = [];
        this.$refs.selectBox.majorList = [];
        res.Datas.forEach(element => {
          let temp = [];
          element.Value.forEach(ele => {
            temp.push({
              check: false,
              id: ele.CourseID,
              text: ele.CourseName
            });
          });
          this.$refs.selectBox.majorList.push({
            check: false,
            id: element.ClassifyID,
            text: element.ClassifyName,
            course: temp
          });
        });
        this.$refs.selectBox.majorcourseList = this.$refs.selectBox.majorList[0].course;
