    border-bottom: 1px solid #ddd;
    text-align: left;
    padding: 8px 20px;
    background: #fff;
    .bar-input {
      width: 225px;
      button {
        background: #4ea9ff;
        color: #fff;
        padding: 9px 12px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
      input {
        height: 32px !important;
      }
    }
    .sort-type {
      color: #aaa;
      margin-left: 25px;
      font-size: 0.8rem;
